import { Component, Inject } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { BooleanStatus } from '@core/enums';
import { SendInquiry } from '@core/models';
import { MyEnquiriesFacade } from '@feature/my-enquiries/my-enquiries.facade';
import { ButtonType } from '@shared/components/button/button.component';
import { SharedAdsFacade } from '@shared/facades/shared-ads.facade';

export interface DialogData {
	noTenantinLinkCount: string;
	enquiryData: SendInquiry;
	adId: string;
	enquiryId: string;
	myEnquiriesFacade: MyEnquiriesFacade;
	sharedAdsFacade: SharedAdsFacade;
}

@Component({
    selector: 'findqo-apply-tenantin-link-dialog',
    templateUrl: './apply-tenantin-link-dialog.component.html',
    styleUrls: ['./apply-tenantin-link-dialog.component.scss'],
    standalone: false
})
export class ApplyTenantinLinkDialogComponent {
	constructor(@Inject(MAT_DIALOG_DATA) public readonly dialogData: DialogData, private readonly dialogRef: MatDialogRef<ApplyTenantinLinkDialogComponent>) {}

	readonly buttonType = ButtonType;

	onReSubmitEnquiry(isApplyAll: boolean): void {
		const UPDATED_ENQUIRY_DATA = { ...this.dialogData.enquiryData, applyToAll: isApplyAll ? BooleanStatus.TRUE : BooleanStatus.FALSE };

		this.dialogData.sharedAdsFacade.reSubmitEnquiry(
			this.dialogData?.adId,
			this.dialogData?.enquiryId,
			UPDATED_ENQUIRY_DATA,
			this.dialogData?.myEnquiriesFacade
		);
		this.dialogRef.close();
	}
}
