import { HttpErrorResponse } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { AdsApi } from '@core/data-access/apis/ads.api';
import { AuthApi } from '@core/data-access/apis/auth.api';
import { ConfigState } from '@core/data-access/states/config.state';
import { AdListKind, BooleanStatus } from '@core/enums';
import {
	Ad,
	CommonItem,
	CommonResponse,
	CreateTenantProfileResponse,
	Inquiry,
	PageMetaData,
	ReportAd,
	SendEnquiryDialogData,
	SendInquiry,
	SendTenantOtp,
	TenantProfile,
	VerifyEmailOtpRequest,
} from '@core/models';
import { SnackService } from '@core/services/snack.service';
import { ApplyTenantinLinkDialogComponent } from '@feature/general/send-inquiry/apply-tenantin-link-dialog/apply-tenantin-link-dialog.component';
import { SendInquiryDialogComponent } from '@feature/general/send-inquiry/send-inquiry-dialog.component';
import { MyEnquiriesFacade } from '@feature/my-enquiries/my-enquiries.facade';
import { AdReportDialogComponent } from '@shared/components/ads/ad-report-dialog/ad-report-dialog.component';
import { AdsState } from '@shared/states/ads.state';
import { Observable, catchError, combineLatest, distinctUntilChanged, firstValueFrom, map, throwError } from 'rxjs';

export interface AdsViewModel {
	adList: Ad[];
	metadata: PageMetaData;
	adsCount: number;
	isLoading: boolean;
	isEmptyList: boolean;
}

@Injectable()
export class SharedAdsFacade {
	constructor(
		private readonly adsState: AdsState,
		private readonly configState: ConfigState,
		private readonly adsApi: AdsApi,
		private readonly snackService: SnackService,
		private matDialog: MatDialog,
		private readonly authApi: AuthApi
	) {}

	/**
	 * Ads View Model
	 */
	getAdsVm$(): Observable<AdsViewModel> {
		return combineLatest({
			adList: this.adsState.adList$.pipe(distinctUntilChanged()),
			metadata: this.adsState.pageMetaData$.pipe(distinctUntilChanged()),
			adsCount: this.adsState.adsCount$.pipe(distinctUntilChanged()),
			isLoading: this.adsState.isLoading$.pipe(distinctUntilChanged()),
			isEmptyList: this.isEmptyAdList$().pipe(distinctUntilChanged()),
		}).pipe(
			map(({ adList, metadata, adsCount, isLoading, isEmptyList }) => {
				return { adList, metadata, adsCount, isLoading, isEmptyList };
			})
		);
	}

	getAdList$(): Observable<Ad[]> {
		return this.adsState.adList$;
	}

	getAd$(): Observable<Ad> {
		return this.adsState.ad$;
	}

	updateAd(data) {
		this.adsState.updateAd(data);
	}

	isEmptyAdList$(): Observable<boolean> {
		return this.adsState.adListKind$.pipe(map((kind: AdListKind) => kind === AdListKind.SIMILAR));
	}

	isLoading$(): Observable<boolean> {
		return this.adsState.isLoading$;
	}

	getAdsCount$(): Observable<number> {
		return this.adsState.adsCount$;
	}

	getPageMetaData$(): Observable<PageMetaData> {
		return this.adsState.pageMetaData$;
	}

	getPageMetaData(): PageMetaData {
		return this.adsState.getPageMetaData();
	}

	////TODO: move all this to a new SharedEnquiriesFacade file
	loadInquiry(adId: string): Promise<{ data: Inquiry[] | Inquiry; meta: PageMetaData }> {
		return firstValueFrom(this.adsApi.getInquiry({ adId: adId }));
	}

	getReportAdReasons$(): Observable<CommonItem[]> {
		return this.adsState.reportAdReasons$;
	}

	addFavouriteAdFromList(ads: Ad): void {
		this.adsApi.saveFavoriteAd(ads).subscribe(() => {
			this.adsState.updateAdList({
				...ads,
				isFavorite: '1',
			});

			this.snackService.open({ data: { message: 'Added to favorites' }, panelClass: ['snackbar-success'] });
		});
	}

	deleteFavouriteAdFromList(ad: Ad, dialogRef?: MatDialogRef<unknown>): void {
		this.adsApi.deleteFavoriteAd(ad).subscribe(() => {
			this.snackService.open({ data: { message: 'Removed from favorites' }, panelClass: ['snackbar-success'] });

			this.adsState.updateAdList({
				...ad,
				isFavorite: '0',
			});

			if (dialogRef) {
				dialogRef.close();
				this.adsState.deleteAd(ad);
			}
		});
	}

	deleteFavouriteAd(ad: Ad): void {
		this.adsApi.deleteFavoriteAd(ad).subscribe(() => {
			this.snackService.open({ data: { message: 'Removed from favorites', icon: 'info' }, panelClass: ['snackbar-success'] });

			this.adsState.updateAd({
				...ad,
				isFavorite: '0',
			});
		});
	}

	saveFavouriteAd(ad: Ad): void {
		this.adsApi.saveFavoriteAd(ad).subscribe(() => {
			this.snackService.open({ data: { message: 'Added to favorites', icon: 'info' }, panelClass: ['snackbar-success'] });

			this.adsState.updateAd({
				...ad,
				isFavorite: '1',
			});
		});
	}

	//TODO: move all this to a new SharedEnquiriesFacade file
	sendInquiry(adId: string, data: SendInquiry): Promise<CommonResponse<Inquiry>> {
		return firstValueFrom(this.adsApi.sendInquiry(adId, data));
	}

	loadReportAdReasons(): void {
		this.adsApi.getReportAdReasons().subscribe((data) => this.adsState.setReportAdReasons(data));
	}

	reportAd(adId: string, request: ReportAd, matDialogRef: MatDialogRef<AdReportDialogComponent>): void {
		this.adsApi.reportAd(request, adId).subscribe(() => {
			matDialogRef.close();

			this.snackService.open({
				data: { translationKey: 'n3304', type: 'success', icon: 'check_circle' },
				panelClass: 'snackbar-success',
			});
		});
	}

	//TODO: move all this to a new SharedEnquiriesFacade file

	reSubmitEnquiry(adId: string, enquiryId: string, body: SendInquiry, myEnquiriesFacade?: MyEnquiriesFacade): void {
		if (myEnquiriesFacade) {
			myEnquiriesFacade.setIsLoading(true);
		}

		this.adsApi
			.reSubmitEnquiry(adId, enquiryId, body)
			.pipe(
				catchError((error: HttpErrorResponse) => {
					if (myEnquiriesFacade) {
						myEnquiriesFacade.setIsLoading(false);
					}

					this.snackService.open({
						data: { translationKey: 'n608' },
						panelClass: 'snackbar-error',
					});

					throw new Error(error?.message);
				})
			)
			.subscribe(() => {
				//to update the enquiry page if coming from that page
				if (myEnquiriesFacade) {
					myEnquiriesFacade.loadEnquiryList({ page: 1, perPage: 20 });
				}

				this.snackService.open({
					data: { translationKey: 'n3570', icon: 'check_circle' },
					panelClass: 'snackbar-success',
				});
			});
	}

	//TODO: move all this to a new SharedEnquiriesFacade file

	handleEnquiryReSubmit(
		sendInquiryValues: SendInquiry,
		dialogData: SendEnquiryDialogData,
		isShowApplyAllPopup: boolean,
		dialogRef: MatDialogRef<SendInquiryDialogComponent>
	): void {
		//if more enquiries without tnenantin url
		if (+dialogData?.noTenantLinkTotal > 1 && isShowApplyAllPopup && sendInquiryValues?.tenantinEmail) {
			dialogRef.close();

			this.matDialog.open(ApplyTenantinLinkDialogComponent, {
				height: 'auto',
				disableClose: true,
				maxHeight: 'unset',
				data: {
					noTenantinLinkCount: dialogData.noTenantLinkTotal,
					enquiryData: sendInquiryValues,
					adId: dialogData?.ad?.id,
					enquiryId: dialogData?.inquiryData?.id,
					myEnquiriesFacade: dialogData.myEnquiriesFacade,
					sharedAdsFacade: dialogData.sharedAdsFacade,
				},
				panelClass: 'apply-tenantin-link-dialog',
			});
		} else {
			dialogRef.close();

			const UPDATED_SEND_ENQUIRY = { ...sendInquiryValues, applyToAll: BooleanStatus.FALSE };
			this.reSubmitEnquiry(dialogData?.ad?.id, dialogData?.inquiryData?.id, UPDATED_SEND_ENQUIRY, dialogData.myEnquiriesFacade);
		}
	}

	sendTenantOtp(body: SendTenantOtp): Observable<CommonResponse<object>> {
		return this.authApi.sendTenantOtp(body);
	}

	verifyTenantOtp(request: VerifyEmailOtpRequest): Observable<CommonResponse<TenantProfile>> {
		return this.authApi.verifyTenantOtp(request);
	}

	//TODO: Add a appropriate type to the body, in future when we will pass something to body
	createTenantProfile(body: object = {}): Observable<CommonResponse<CreateTenantProfileResponse>> {
		return this.authApi.createTenantProfile(body);
	}

	getPdfLocalUrl(pdfSignedUrl: string): Observable<Blob> {
		return this.adsApi.getPdfContent(pdfSignedUrl).pipe(
			map((res) => {
				return new Blob([res], { type: 'application/pdf' });
			}),
			catchError((err) => {
				this.snackService.open({ data: { message: this.configState.getTranslations()['n608'] }, panelClass: ['snackbar-error'] });
				return throwError(err);
			})
		);
	}
}
